body {
  margin: 0;
  font-family: 'Inter', 'Roboto', sans-serif;
}

.wrap{
  display: flex;
  flex-direction: row;
  padding: 48px;
  gap: 24px;
  height: 80vh;
}

.wrap > div{
  padding: 24px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.sidebar{
  width: 280px;
  background: #f2f2f2;
  display: flex;
  flex-direction: column;
}

.sidebar .licCat__slices{
  padding: 12px 0 24px; /* gap between category name and slices */

  display: flex;
  flex-direction: column;
  gap: 16px; /* gap between slices */
}

.licSlice{
  background: white;
  padding: 16px;
  border: 2px solid #ccc;
  border-radius: 8px;
}
.licSlice:hover{
  cursor: pointer;
}

.licSlice--selected{
  background:#eef;
  border: 2px solid blue;
  color: #008;
}
.licSlice.licSlice--selected:hover{
  cursor: default;
}

.licSlice__rowTop {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.licSlice__name {
  font-weight: bold;
}

.tableView{
  display: flex;
  flex-direction: column;
  flex:1;
  overflow: hidden;
}

.tableView__scrollArea{
  flex:1;
  overflow: auto;
}

.tableView table{
  
}

.tableView th {
  text-align: left;
}

.tableView tr {
}

.tableView td {
  padding: 16px 12px;
  border-bottom: 1px solid #ccc;
}

button{
  border-radius: 8px;
  padding: 8px;
  border: none;
}

button:hover{cursor: pointer;}
button:disabled{opacity: 0.3}

.btn--neutral{
  background: white;
  border: 2px solid #ccc;
}
.btn--primary{
  background: blue;
  border: 2px solid blue;
  color: white;
}

.u-textRight{
  text-align: right;
}

.meter{
  display: block;
  height: 8px;
  width: 100%;
  background: #ddd;
  overflow: hidden;
  border-radius: 100px;
}
.meter__fill{
  background: blue;
}
.meter__fill:after
{
    content: '.';
}